        line-height: 32px;
        color: #303133;
    }

    .title {
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        color: #303133;
        text-transform: uppercase;
    }
}

.text{
    &__inner{
        padding-top: 40px;
        padding-bottom: 90px;
        font-weight: 400;
        font-size: 20px;
        line-height: 140%;
        color: #303133;

