                font-size: 13px;
            }
        }


        /* width */
        &::-webkit-scrollbar {
            width: 8px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
           background: #E8F2FF;
            border-radius: 5px;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            width: 4px;
            background: #CBDAEF;
            border-radius: 5px;
        }
