}



.blue-btn {
    font-weight: 700;
    font-size: 15px;
    line-height: 180%;
    color: #FFFFFF;
    padding: 7px 35px;
    width: max-content;
    background: #3A75FF;
    border-radius: 16px;
    margin-top: 30px;
    display: block;
    transition: 0.2s linear;

    &:hover {
        box-shadow: 0px 12px 18px rgba(58, 117, 255, 0.4);
        background: #3A75FF;
    }
}




.copy-link {
    position: relative;

    &::before {
        position: absolute;
        content: 'Ссылка скопирована';
        background: #555;
        padding: 10px 15px;
        width: max-content;
        border-radius: 5px;
        color: #fff;
        top: -45px;
        left: 50%;
        transform: translateX(-50%) translateY(20px);
        font-size: 12px;
        opacity: 0;
        transition: 0.1s linear;
    }
}

.copy-link.visible {
    &::before {
        transform: translateX(-50%) translateY(0px);
        opacity: 1;
    }

}

.examples {
    margin-top: 70px;
    &-title{
        font-weight: 400;
        font-size: 26px;
        line-height: 140%;
        color: #303133;
        padding-left: 20px;
        b{
            font-weight: 600;
        }

        @media(max-width: 768px) {
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            margin-bottom: 20px;
        }
    }
    @media(max-width: 768px) {
        margin-top: 40px;
        margin-bottom: 50px;
    }

    &-slider{
        overflow: hidden;
        padding: 20px;
        @media(max-width: 768px){
            padding: 10px;
        }
        &-nav{
            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            margin-top: 20px;
            @media(max-width: 768px) {
                margin-top: 15px;
            }
            .prev-slide, .next-slide{
                width: 24px;
                height: 24px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                &:hover{
                    svg{
                        path{
                            stroke: #3A75FF;
                        }
                    }
                }
            }

            .slider-pagination{
                width: max-content !important;
                display: flex;
                align-items: center;
                margin: 0px 10px;
                .swiper-pagination-bullet{
                    width: 11px;
                    height: 11px;
                    margin-top: 0;
                    cursor: pointer;
                    background: rgba(160, 182, 209, 0.31);
                    opacity: 1 !important;

                    &.swiper-pagination-bullet-active{
                        background: #3A75FF;
                    }
                    &:hover{
                        background: #1353E9;
                    }
                }

                
            }
        }
    }

    .container{
        max-width: 1040px;
        @media(max-width: 768px){
            padding: 0;
        }
    }



    &__slider-wrapper{
        display: flex;

      
    }

    &__slide{
        width: 50%;
        background: #FFFFFF;
        border: 1px solid #F7F7F7;
        border-radius: 16px;
        padding: 30px;
        flex-shrink: 0;
        filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.04)) drop-shadow(0px 8px 16px rgba(0, 0, 0, 0.08));

        @media(max-width: 768px) {
            width: 100%;
            padding: 15px;
        }

        @media(max-width: 768px){
            min-height: 280px;
        }

        .title {
            font-weight: 700;
            font-size: 20px;
            line-height: 24px;
            color: #303133;

            @media(max-width: 768px) {
                font-weight: 700;
                font-size: 16px;
                line-height: 160%;
            }
        }

        .text {
            font-weight: 400;
            font-size: 15px;
            line-height: 180%;
            color: #303133;
            margin-top: 20px;

            @media(max-width: 768px) {
                margin-top: 15px;
                font-weight: 400;
                font-size: 14px;
                line-height: 180%;
            }
        }
    }
}



.faq {
    margin-top: 80px;
    @media(max-width: 768px){
        margin-top: 40px;
    }
    &-title {
        font-weight: 700;
        font-size: 28px;
        line-height: 140%;
        color: #303133;
        margin-bottom: 50px;
        @media(max-width: 768px){
            margin-bottom: 25px;
        }
    }

    .ac {
        background: #E8F2FF;
        border-radius: 16px;
        border: none;
        transition: 0.2s linear;
        font-family: 'Montserrat', sans-serif;

        .ac-trigger {
            border: none;
            border-radius: none;
            padding: 21px;
            padding-left: 60px;
            border: none;
            font-weight: 500;
            font-size: 20px;
            line-height: 140%;
            color: #303133;
            font-family: 'Montserrat', sans-serif;

            @media(max-width: 768px) {
                font-size: 15px !important;
                padding: 15px;
                padding-left: 45px;
            }

            &:after {
                content: '';
                right: auto;
                left: 15px;
                background-image: url("data:image/svg+xml,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 8.5V17M17 17V25.5M17 17H25.5M17 17H8.5' stroke='%233A75FF' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
                width: 34px;
                height: 34px;
                background-size: cover;

                @media(max-width: 768px) {
                    left: 5px;
                }
            }
        }

        .ac-text {
            font-weight: 400;
            font-size: 15px;
            line-height: 180%;
            color: #303133;
            padding-left: 60px;
            padding-right: 60px;
            padding-top: 0;
            padding-bottom: 30px;
            font-family: 'Montserrat', sans-serif;
            ul{
                font-weight: 400;
                font-size: 15px;
                line-height: 180%;
                color: #303133;
                padding-right: 60px;
                padding-top: 0;
                padding-bottom: 30px;
                @media(max-width: 768px) {
                    padding-right: 0;
                }
                li{
                    position: relative;
                    padding-left: 20px;
                    @media(max-width: 768px) {
                        font-size: 13px !important;
                    }
                    &::before{
                        content: '';
                        position: absolute;
                        display: block;
                        width: 3px;
                        height: 3px;
                        border-radius: 50%;
                        top: 12px;
                        left: 0;
                        background: #303133;
                    }
                }


            }
            @media(max-width: 768px) {
                font-size: 13px !important;
                padding-left: 45px;
            }
        }
    }

    .ac.is-active>.ac-header .ac-trigger::after {
        content: '';
        background-image: url("data:image/svg+xml,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M25.5 17H17H8.5' stroke='%234EC5DE' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") !important;
    }

    .ac.is-active {
        background: #F4F8FE;
    }


}


