.example-popup-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;

    opacity: 0;
    visibility: hidden;
    transition: 0.2s linear;
    @media(max-width: 768px){
        display: flex;
        align-items: flex-end;
       padding: 10px;
    }
    &.opened{
        opacity: 1;
        visibility: visible;
      
    }
    .example-popup-overlay{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: rgba(48, 49, 51, 0.6);
        z-index: 1;
    }

    .example-popup{
        position: absolute;
        left: 50%;
        transform: translate(-50%, -50%);
        top: 50%;
        max-width: 498px;
        width: 100%;
        z-index: 5;
       @media(max-width: 768px){
        height: max-content;
        top: auto;
        position: static !important;
        transform: translateX(0);
       }
    }

    .example-body{
        background: #FFFFFF;
        border-radius: 16px;
        margin-bottom: 6px;
    }

    .example-popup-title{
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        color: #303133;
        padding: 20px 25px;
        border-bottom: 1px solid #E9E9E9;
        @media(max-width: 768px){
            padding: 20px;
        }
    }

    .example-popup-content{
        padding: 20px 25px;
        padding-bottom: 30px;
        font-weight: 400;
        font-size: 15px;
        line-height: 180%;
        color: #303133;
        @media(max-width: 768px){
            padding: 20px;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
        }
        b{
            font-weight: 600;
        }
    }


    .example-close{
        background: #FFFFFF;
        border-radius: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 55px;

        font-weight: 600;
        font-size: 18px;
        line-height: 140%;
        text-align: center;
        color: #303133;

        @media(max-width: 768px){
            font-weight: 600;
            font-size: 18px;
            line-height: 140%;
        }
    }
}









